/* Start of ChatRoom Styles */
.wrapper {
    position: relative;
    display: flex;
    flex-direction: column;
    width: 100%;
    height: 100vh;
    align-items: center;
    background: #101010;
    background-size: cover;
    overflow: visible !important;
  } 

  .bg {
    opacity: 0.09;
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: auto;
  }

  .blur {
    background: rgba(255, 255, 255, 0.25); 
    -webkit-backdrop-filter: blur(7px); 
            backdrop-filter: blur(7px); 
    height: 100vh;
    width: 100%;
  }

  .content-wrapper {
    position: relative;
    display: flex;
    align-items: center;
    flex-direction: column;
    width: 100%;
  }

  .navbar-container {
      width: 98%;
      height: 75px;
      display: flex;
      justify-content: space-between;
      align-items: center;
      background: #121212;
      border-radius: 34px; 
      box-shadow: 0px 9px 12px 7px rgba(0,0,0,0.25);
  }

  .company-name {
      font-family: 'Mulish', sans-serif;
      font-weight: 700;
      font-size: 20px;
      color: white;
      margin-left: 68px;
  }

  .bluey {
      color: #52A3EE;
  }

  .logout-container {
      margin-right: 68px;
  }

  .logout {
    background: #dadee5;
    width: 134px;
    height: 36px;
    border-radius: 50px;
    border: none;
    cursor: pointer;
    font-family: 'Mulish', sans-serif;
    font-weight: 700;
    font-size: 18px;
    letter-spacing: 0.03em;
  }
    
  span {
      position: relative;
  }

  .animate.shake:hover {
    animation: shake .6s ease-in-out both;
  }
  
  .animate.shake:hover span {
    animation: shakeText .5s ease-in-out both;
    animation-delay: 0.05s;
  }

  @keyframes shake {
    25% {
      transform: rotate(3deg);
    }
    50% {
      transform: rotate(-3deg);
    }
    100% {
      transform: rotate(0deg);
    }
  }
  
  @keyframes shakeText {
    0% {
      right: 4px;
    }
    33% {
      right: -4px;
    }
    66% {
      right: 4px;
    }
    100% {
      right: 0;
    }
  }
  
  .logout-primary {
    background: #101010;
    color: white;
    border: 1.5px solid #929292; 
    box-shadow: 0px 4px 9px -2px rgba(0,0,0,0.25);
  }

  .video-wrapper {
      display: flex;
      justify-content: center;
      align-items: center;
      flex-wrap: wrap;
  }

  .video-wrapper2 {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;
    grid-gap: 12px;
    gap: 12px;
    margin-left: 244px;
}

  .video-container {
      margin-top: 166px; 
      width: 468px;
      height: 390px;
      background: black;
      border: 1px solid rgba(173,172,172,0.4);
      border-radius: 40px;
  }

  .videoplayer-container {
      display: flex;
      justify-content: center;
      margin-top: -50px;
  }

  .video {
    width:100%;
    height:100%;
    object-fit: cover;
    overflow: hidden;
    border-radius: 40px;
  }

  .username {
      position: relative;
      width: 123px;
      height: 36px;
      background: rgba(53, 53, 53, 0.44);
      bottom: 60px;
      left: 30px;
      border-radius: 24px;
  }

  .user {
      font-family: 'Mulish', sans-serif;
      font-weight: 500;
      font-size: 18px;
      letter-spacing: 0.03em;
      color: white;
      text-align: center;
      padding-top: 6px;
  }

  .options-container {
      padding: 14px 45px;
      width: 769px;
      height: -webkit-fit-content;
      height: -moz-fit-content;
      height: fit-content;
      background: #121212;
      margin-top: 100px;
      padding-bottom: 20px;
      border-radius: 16px; 
      box-shadow: 0px 7px 7px 2px rgba(0,0,0,0.25);
  }

  .form-wrapper {
    width: 100%;
    display: grid;
    grid-template-columns: repeat(2,1fr);
    grid-row-gap: 2rem;
    grid-row-gap: 8rem;
    row-gap: 8rem;
    grid-column-gap: 4rem;
    -webkit-column-gap: 4rem;
    grid-column-gap: 4rem;
    column-gap: 4rem;
  }

  .account-info {
      font-family: 'Mulish', sans-serif;
      font-size: 20px;
      font-weight: 500;
      letter-spacing: 0.03em;
      color: white;
  }

  input:focus::-webkit-input-placeholder {
    color: transparent;
  }

  input:focus:-ms-input-placeholder {
    color: transparent;
  }

  input:focus::placeholder {
    color: transparent;
  }

  .input {
      margin-top: 20px;
      padding-bottom: 8px;
      background: transparent;
      border: none;
      outline: none;
      font-family: 'Mulish', sans-serif;
      font-size: 18px;
      font-weight: 500;
      letter-spacing: 0.03em;
      color: #707070;
      border-bottom: 1px solid #707070;
      width: 100%;
  }

  .margin-button {
      margin-top: 20px;
  }

  .feature-btn {
    background: #dadee5;
    width: 100%;
    height: 56px;
    border-radius: 50px;
    border: none;
    cursor: pointer;
    font-family: 'Mulish', sans-serif;
    font-weight: 500;
    font-size: 18px;
    letter-spacing: 0.03em;
  }

  .feature-primary {
    background: #101010;
    color: white;
    border: 1.5px solid #929292; 
    box-shadow: 0px 4px 9px -2px rgba(0,0,0,0.25);
  }

  .incoming-call {
      display: flex;
      justify-content: center;
      margin-top: 18px;
  }

  .incoming-call h1 {
      font-family: 'Mulish', sans-serif;
      color: white;
      font-size: 25px;
      font-weight: 500;
      letter-spacing: 0.03em;
      margin-right: 10px;
  }

  .answer-btn {
    background: #dadee5;
    width: 100px;
    height: 40px;
    border-radius: 50px;
    border: none;
    cursor: pointer;
    font-family: 'Mulish', sans-serif;
    font-weight: 700;
    font-size: 16px;
    letter-spacing: 0.03em;
  }

  .answer-btn-primary {
    background: #101010;
    color: white;
    border: 1.5px solid #929292; 
    box-shadow: 0px 4px 9px -2px rgba(0,0,0,0.25);
  }

  .user-features {
      display: flex;
      justify-content: center;
      align-items: center;
      margin-top: 163px;
  }

  .mute-btn {
      background: #F8002D !important;
      width: 82px !important;
      height: 78px !important;
      transition: all .1s ease-in;
      cursor: pointer;
  }

  .mute-btn:hover {
      background: #88031b !important;
  }

  .user-btn {
      background: #121212;
      width: 68px;
      height: 65px;
      border-radius: 11px;
      border: none;
      transition: all .2s ease-in-out; 
      box-shadow: 0px 4px 9px -2px rgba(0,0,0,0.25);
      cursor: pointer;
  }

  .user-btn:hover {
    background: #121212a9;
  }

  .user-btn + .user-btn {
      margin-left: 62px;
  }

  .icon-color {
      color: white;
  }

  @media screen and (max-width: 1790px) {
      wrapper {
          width: 70%;
      }

